import React from 'react';
import './profileFeed.scss';
import Post from '../subComponents/post/post';
import moon from '../../resources/img/moon.png';
import arrow from '../../resources/img/arrow_back.svg';
import office from '../../resources/img/office.png';
import profile from '../../resources/img/photoOfProfile.svg';
import Button from '../subComponents/button/button';

const data = [
    {id:1, name:" ", name_year:"@founder  1935 .", text:"       .    ,   ."},
    {id:2, name:" ", name_year:"@founder  1897 .", text:",  ..."},
    {id:3, name:" ", name_year:"@founder  1884 .", text:"    ,  -     "},
    {id:4, name:" ", name_year:"@founder  17  1882 .", text:"   . "},
    {id:5, name:" ", name_year:"@founder  17  1882 .", text:"     ?    ?  -   ."}
]

const ProfileFeed = () => {
    return (
        <div className='profileFeed'>
            <div className='top_line'>
                <img src={arrow} alt=''/>
                <div className='lineName'>
                    <div className='name'> </div>
                    <div className='kolPosts'>5000 </div>
                </div>
            </div>
            <div className='mainProfile'>
                <div className='mainProfile__cover'>
                    <div><img className='office' src={office} alt=''/></div>
                    <div><img className='profileImg' src={profile} alt=''/></div>
                </div>
                <div className='infoProfile'>
                    <div className='left'>
                        <div className='name'> </div>
                        <div className='nic_name'>@founder</div>
                        <div className='about_me'>  ,    .</div>
                        <div className='readers'>
                            <div className='reader'><p>1000 <span></span></p></div>
                            <div className='reader'><p>1 <span></span></p></div>
                        </div>
                    </div>
                    <div className='right'>
                        <Button> </Button>
                    </div>
                </div>
            </div>
            <div className='posts'>
                <div className='catalog'>
                    <div className='catalog__item catalog__item_active'></div>
                    <div className='catalog__item'></div>
                </div>
                <Post profile={profile} name=" " name_year="@founder &bull; 1973 ." text=" ,      ." moon={moon}/>
                {data.map((val) => {
                    return (
                        <Post key={val.id} name={val.name} name_year={val.name_year} 
                        text={val.text} moon="" profile={profile}/>
                    )
                })}
            </div>
        </div>
    );
};

export default ProfileFeed;